<template>
  <div class="main">
    <v-header class="header" :seller="seller"></v-header>
    <div class="tab">
      <div class="tab-item" @click.stop="goToPage(0)">商品</div>
      <div class="tab-item" @click.stop="goToPage(1)">评论</div>
      <div class="tab-item" @click.stop="goToPage(2)">商家</div>
    </div>
    <div class="content" ref="content">
      <div class="content_wrapper" ref="contentWrapper">
        <div class="component_item" v-for="item in componentList" ref="componentItem">
          <!--动态引用组件-->
          <component :is="item"></component>
        </div>
      </div>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
  import header from './header/header.vue'
  import BScroll from 'better-scroll'
  import goods from './goods/goods.vue'
  import ratings from './ratings/ratings.vue'
  import seller from './seller/seller.vue'

  export default {
    name: 'v-main',
    data() {
      return {
        seller: {},
        componentList: ['goods','ratings','seller'],
        scroll: null
      }
    },
    created() {
      this.$http.get('/api/seller').then((response) => {
        let res = response.body
        if (res.errno === 0) {
          this.seller = res.data
        }
      })
    },
    components: {
      'v-header': header,
      goods,
      ratings,
      seller
    },
    computed: {},
    methods: {
      InitTabScroll() {
        //横向滚动必须先计算宽度
        let width = 0
        for (let i = 0; i < this.componentList.length; i++) {
          width += this.$refs.componentItem[0].getBoundingClientRect().width; //getBoundingClientRect() 返回元素的大小及其相对于视口的位置
        }
        this.$refs.contentWrapper.style.width = width + 'px'
        this.$nextTick(() => {
          if (!this.scroll) {
            this.scroll = new BScroll(this.$refs.content, {
              startX: 0,
              click: true,
              scrollX: true,
              scrollY: false,
              eventPassthrough: 'vertical',
              snap: {
                loop: false,
                threshold: 0.1,
                speed: 400
              }
            });
          } else {
            this.scroll.refresh()
          }
        });
      },
      goToPage(index){
        this.scroll.goToPage(index)
      }
    },
    mounted() {
      this.$nextTick(() => {
        this.InitTabScroll();
      })
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang='scss' scoped>
  .router-link-active {
    color: red;
  }

  .main {
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;

    .tab {
      width: 100%;
      height: .4rem;
      display: flex;
      .tab-item {
        flex: 1;
        text-align: center;
        font-size: .14rem;
        line-height: .4rem;
      }
    }

    .content {
      width: 100vw;
      height: calc(100vh - 1.74rem);
      overflow: hidden;
    }

    .content_wrapper {
      display: flex;
      height: 100%;
    }

    .component_item {
      flex: 0 0 360px;
      width: 360px;
    }
  }
</style>



